.sigup {
    font-family: $font-2;
    padding: 0 20px 20px;

    .logo {
        display: flex;
        justify-content: center;
        margin: 0;
        img {
            height: 180px;
            width: 180px;
            
        }
    }
    form {
        width: 80%;
        margin: 0 auto;
        // min-width: 380px;        max-width: 600px;
        // background: white;
        display: grid;
        grid-template-rows: repeat(4, 104px);
        grid-row-gap: 15px;
        padding: 30px;
        border-radius: 16px;
        box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
        position: relative;
        // margin-top: 150px;
        border: 1px solid #11111135;
        

        
        
    }

    
    div {
    transition: 0.2s;
    }
    label {
    letter-spacing: 1px;
    
    }
    input[type="text"],
    input[type="password"] {
    margin: 7px 0 7px;
    border: 1px solid transparent;
    background: rgb(235, 244, 242);
    height: 40px;
    width: 90%;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    border-radius: 9px;
    font-size: 1.1rem;
    padding-left: 10px;
    transition: 0.2s;

    }
    input[type="text"]:focus,
    input[type="password"]:focus {
    border: 1px solid #5ef9a4;

    }
    span {
    margin-bottom: 20px;
    display: block;
    font-size: 0.8rem;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
    color: red;
    }
    p {
    background: red;
    width: 33%;
    height: 6px;
    margin: -2px 0 2px;
    border-radius: 50px;
    visibility: hidden;
    transition: 0.3s;
    }
    #security span {
    display: inline;
    }
    input[type="submit"] {
    border: none;
    background: #173153;
    width: 50%;
    margin: 12px auto 0;
    padding: 10px;
    border-radius: 15px;
    font-size: 1.2rem;
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    color: whitesmoke;
    transition: 0.3s ease;
    }
    input[type="submit"]:hover {
    filter: brightness(105%);
    transform: translateX(2px);
    }
    .error input {
    color: red;
    border: 1px solid rgba(255, 0, 0, 0.527);
    }
    .error span {
    visibility: visible;
    opacity: 1;
    color: red;
    }
    .progressRed {
    background: red;
    width: 33%;
    visibility: visible;
    }
    .progressBlue {
    background: #16d9e7;
    width: 67%;
    visibility: visible;
    }
    .progressGreen {
    width: 100%;
    background: #5ef9a4;
    visibility: visible;
    }

    div {
        margin-top: 10px;
        text-align: center;

        h4 {
            display: inline;
        }
    }
}